<script>
export default {
  data() {
    return {
      search: "",
      sortBy: "",
      keys: ["Code", "Date", "Name"],
      headers: [
        { text: "Code", align: "start", value: "code" },
        { text: "Date", value: "date" },
        { text: "Name", value: "name" },
        { text: "Payment", value: "payment" },
        { text: "Order Price", value: "orderPrice" },
        { text: "D/C Price", value: "dcPrice" },
        { text: "Amount of Payment", value: "amount" },
        { text: "State", value: "state", sortable: false },
        { text: "Shipping information URL", value: "url", sortable: false },
        { text: "Select", value: "checkbox", sortable: false },
        { text: "Info", value: "actions", sortable: false },
      ],
      dateStart: '',
      dateEnd: '',
      menuStart: false,
      menuEnd: false,
      dialog: false,
      titleDialogExcel: "Do you want to get the entire data into Excel?",
      dialogExcel: false,
    };
  },

  methods: {
    resetInput() {
      let inputCheckbox = document.querySelectorAll(
        ".table_head .v-input--selection-controls__input input"
      );

      inputCheckbox.forEach((element) => {
        if (element.checked) {
          element.click();
        }
      });

      this.search = "";
      this.sortBy = "";
      this.dateStart = "";
      this.dateEnd = "";
    },
    showDialog(item) {
      this.dialog = true;
    },
    closeDialog() {
      this.dialog = false;
    },
    showDialogExcel() {
      let isChecked = document.querySelector(".table_checkbox_current input");
      if (isChecked.checked) {
        this.titleDialogExcel =
          "Do you want to get the current search results in Excel?";
      } else {
        this.titleDialogExcel =
          "Do you want to get the entire data into Excel?";
      }
      this.dialogExcel = true;
    },
    closeDialogExcel() {
      this.dialogExcel = false;
    },
  },
};
</script>
